
#meh{
  position: relative;
  top: 20px;
  text-align: center;
  width: 100%
}

.welcome{
  max-width: 400px;
  margin-top: 100px
}
.welcome button{
  margin-top: 20px
}
.welcome h2 p{
  font-size: 0.5em
}

.chat h2{
  font-size: 2.6em;
  margin-bottom: 40px;
}
.chat span{
  font-size: 1.8em;
}
.chat .time{
  display: block;
  font-size: 1em
}
.messages{
  max-height: 50vh;
  overflow: auto
}
.messages::-webkit-scrollbar{
  width: 3px
}
.messages::-webkit-scrollbar-track{
  background: #ddd
}
.messages::-webkit-scrollbar-thumb{
  background: #aaa
}















































